.chartBar-main{
    height : 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.chart-Bar{
    height : 100%;
    width : 100%;
    border: 1px solid rgb(71, 68, 68);
    display: flex;
    background-color: rgb(226, 59, 226);
    overflow: hidden;
    border-radius: 12px;
    flex-direction: column;
    justify-content: flex-end;
}
.chart-Bar-fill{
    background-color: blueviolet;
    width : 100%;
    transition :all 1s ease-out;
}
.Label{
    margin-bottom: 5px;
    text-align: center;
}